import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import { useRegistrationStore } from '../store/registrationStore';
import QRCode from 'qrcode';
import { jsPDF } from 'jspdf';

declare global {
  interface Window {
    Razorpay: any;
  }
}

function Register() {
  const navigate = useNavigate();
  const { setRegistrationData, setPaymentStatus } = useRegistrationStore();
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    college: '',
    phone: '',
    numberOfPeople: 1,
  });

  const generateQRCode = async (ticketId: string) => {
    try {
      const qrData = `TarangaCpdsu${ticketId}`;
      return await QRCode.toDataURL(qrData);
    } catch (err) {
      console.error('Error generating QR code:', err);
      return '';
    }
  };

  const generateTicketPDF = async (ticketId: string, qrCodeData: string) => {
    const doc = new jsPDF();
    const ticketWidth = 210; // A4 width in mm
    const margin = 20;

    // Add header
    doc.setFontSize(24);
    doc.text('TARANGA 2024 Ticket', ticketWidth / 2, margin, { align: 'center' });

    // Add ticket details
    doc.setFontSize(12);
    doc.text(`Name: ${formData.name}`, margin, margin + 20);
    doc.text(`Email: ${formData.email}`, margin, margin + 30);
    doc.text(`College: ${formData.college}`, margin, margin + 40);
    doc.text(`Phone: ${formData.phone}`, margin, margin + 50);
    doc.text(`Number of People: ${formData.numberOfPeople}`, margin, margin + 60);
    doc.text(`Ticket ID: ${ticketId}`, margin, margin + 70);

    // Add QR code
    const img = new Image();
    img.src = qrCodeData;
    doc.addImage(img, 'PNG', margin, margin + 80, 50, 50);

    // Save PDF
    doc.save(`taranga_ticket_${ticketId}.pdf`);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: name === 'numberOfPeople' ? Math.min(Math.max(1, Number(value)), 10) : value,
    }));
  };

  const initializeRazorpay = () => {
    return new Promise((resolve) => {
      const script = document.createElement('script');
      script.src = 'https://checkout.razorpay.com/v1/checkout.js';
      script.onload = () => {
        resolve(true);
      };
      script.onerror = () => {
        resolve(false);
      };
      document.body.appendChild(script);
    });
  };

  const handlePayment = async (e: React.FormEvent) => {
    e.preventDefault();
    
    const res = await initializeRazorpay();
    if (!res) {
      alert('Razorpay SDK failed to load');
      return;
    }

    const amount = formData.numberOfPeople * 500;
    setRegistrationData(formData);
    setPaymentStatus('processing');

    const paymentData = {
      amount: amount * 100,
      currency: 'INR',
      receipt: `receipt_${Date.now()}`,
    };

    const options = {
      key: import.meta.env.VITE_RAZORPAY_KEY_ID,
      amount: paymentData.amount,
      currency: paymentData.currency,
      name: 'TARANGA 2024',
      description: 'Cultural Festival Registration',
      handler: async function(response: any) {
        setPaymentStatus('completed');
        
        // Generate ticket ID and QR code
        const ticketId = `R${Date.now().toString().slice(-3)}`;
        const qrCodeData = await generateQRCode(ticketId);
        
        // Generate and save PDF ticket
        await generateTicketPDF(ticketId, qrCodeData);
        
        navigate('/registration-success');
      },
      prefill: {
        name: formData.name,
        email: formData.email,
        contact: formData.phone,
      },
      theme: {
        color: '#FF3366',
      },
    };

    const paymentObject = new window.Razorpay(options);
    paymentObject.open();
  };

  return (
    <div className="min-h-screen pt-24 md:pt-32 pb-16 px-4">
      <div className="max-w-3xl mx-auto">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-center mb-8 md:mb-12"
        >
          <h1 className="text-4xl md:text-6xl font-bold mb-4 md:mb-6">Register for TARANGA</h1>
          <p className="text-lg md:text-xl text-gray-400">
            Join us for an unforgettable cultural experience
          </p>
        </motion.div>

        <motion.form
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
          onSubmit={handlePayment}
          className="bg-gradient-to-br from-[#FF3366]/10 to-transparent p-6 md:p-8 rounded-3xl border border-white/10"
        >
          <div className="space-y-6">
            <div>
              <label className="block text-sm font-medium text-gray-400 mb-2">
                Full Name
              </label>
              <input
                type="text"
                name="name"
                required
                value={formData.name}
                onChange={handleInputChange}
                className="w-full px-4 py-3 bg-black/30 rounded-xl border border-white/10 focus:border-[#FF3366] focus:outline-none focus:ring-1 focus:ring-[#FF3366] transition-colors"
                placeholder="Enter your full name"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-400 mb-2">
                Email
              </label>
              <input
                type="email"
                name="email"
                required
                value={formData.email}
                onChange={handleInputChange}
                className="w-full px-4 py-3 bg-black/30 rounded-xl border border-white/10 focus:border-[#FF3366] focus:outline-none focus:ring-1 focus:ring-[#FF3366] transition-colors"
                placeholder="Enter your email"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-400 mb-2">
                College Name
              </label>
              <input
                type="text"
                name="college"
                required
                value={formData.college}
                onChange={handleInputChange}
                className="w-full px-4 py-3 bg-black/30 rounded-xl border border-white/10 focus:border-[#FF3366] focus:outline-none focus:ring-1 focus:ring-[#FF3366] transition-colors"
                placeholder="Enter your college name"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-400 mb-2">
                Phone Number
              </label>
              <input
                type="tel"
                name="phone"
                required
                pattern="[0-9]{10}"
                value={formData.phone}
                onChange={handleInputChange}
                className="w-full px-4 py-3 bg-black/30 rounded-xl border border-white/10 focus:border-[#FF3366] focus:outline-none focus:ring-1 focus:ring-[#FF3366] transition-colors"
                placeholder="Enter 10-digit phone number"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-400 mb-2">
                Number of People
              </label>
              <input
                type="number"
                name="numberOfPeople"
                min="1"
                max="10"
                required
                value={formData.numberOfPeople}
                onChange={handleInputChange}
                className="w-full px-4 py-3 bg-black/30 rounded-xl border border-white/10 focus:border-[#FF3366] focus:outline-none focus:ring-1 focus:ring-[#FF3366] transition-colors"
              />
              <p className="text-sm text-gray-400 mt-1">Maximum 10 people per registration</p>
            </div>

            <div className="pt-4">
              <div className="bg-black/30 p-4 rounded-xl mb-6">
                <h3 className="text-lg font-semibold mb-2">Registration Summary</h3>
                <div className="flex justify-between items-center">
                  <span>Price per person</span>
                  <span>500</span>
                </div>
                <div className="flex justify-between items-center mt-2">
                  <span>Number of people</span>
                  <span>{formData.numberOfPeople}</span>
                </div>
                <div className="border-t border-white/10 mt-4 pt-4">
                  <div className="flex justify-between items-center font-bold">
                    <span>Total Amount</span>
                    <span className="text-[#FF3366]">{formData.numberOfPeople * 500}</span>
                  </div>
                </div>
              </div>
              <button
                type="submit"
                className="w-full bg-[#FF3366] px-8 py-4 rounded-xl font-semibold hover:bg-[#FF3366]/90 transition-all duration-300 transform hover:scale-105"
              >
                Proceed to Payment
              </button>
            </div>
          </div>
        </motion.form>
      </div>
    </div>
  );
}

export default Register;