import React, { useEffect, useState } from 'react';
import { Star, Sparkles, ArrowRight } from 'lucide-react';
import { motion, useScroll, useTransform } from 'framer-motion';
import { Link } from 'react-router-dom';
import { IMAGES } from '../constants/images';

function Home() {
  const [imagesLoaded, setImagesLoaded] = useState(false);
  const { scrollY } = useScroll();
  const opacity = useTransform(scrollY, [0, 300], [1, 0]);
  const scale = useTransform(scrollY, [0, 300], [1, 1.1]);

  useEffect(() => {
    const preloadImages = async () => {
      const imageUrls = [
        IMAGES.heroImage,
        IMAGES.events.cascades,
        IMAGES.events.cultura,
        IMAGES.events.onam,
        IMAGES.events.kannadaRajotsava
      ];

      const loadImage = (url: string) => {
        return new Promise((resolve, reject) => {
          const img = new Image();
          img.src = url;
          img.onload = resolve;
          img.onerror = reject;
        });
      };

      try {
        await Promise.all(imageUrls.map(loadImage));
        setImagesLoaded(true);
      } catch (error) {
        console.error('Error preloading images:', error);
        setImagesLoaded(true); // Show content anyway
      }
    };

    preloadImages();
  }, []);

  return (
    <div className="overflow-x-hidden">
      {/* Hero Section */}
      <header className="relative min-h-screen">
        <motion.div 
          className="absolute inset-0 overflow-hidden"
          style={{ opacity, scale }}
        >
          <div className="blur-load relative h-full" style={{ backgroundImage: `url(${IMAGES.heroImage}?blur=50)` }}>
            <img 
              src={IMAGES.heroImage}
              alt="Concert crowd"
              className={`w-full h-full object-cover transition-opacity duration-1000 ${imagesLoaded ? 'opacity-100' : 'opacity-0'}`}
              loading="eager"
            />
          </div>
          <div className="absolute inset-0 bg-gradient-to-b from-black/70 via-black/50 to-[#0a0a0a]"></div>
        </motion.div>

        {/* Hero Content */}
        <div className="relative pt-32 flex flex-col items-center justify-center min-h-[80vh] text-center px-4">
          <motion.div 
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
            className="relative"
          >
            <Sparkles className="absolute -top-12 -left-12 w-10 h-10 text-[#FF3366] animate-pulse hidden md:block transition-gpu" />
            <Star className="absolute -right-8 top-0 w-8 h-8 text-yellow-400 animate-spin-slow hidden md:block transition-gpu" />
            <h1 className="text-4xl md:text-8xl font-bold mb-6 leading-tight">
              <span className="block text-2xl md:text-4xl mb-4 text-white">Roy's Project CPA presents</span>
              <span className="block bg-clip-text text-transparent bg-gradient-to-r from-[#FF3366] to-[#FF9933]">
                TARANGA
              </span>
              <span className="block text-2xl md:text-4xl mt-4">2024 Lineup Out Now!</span>
            </h1>
          </motion.div>
          <motion.p 
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.3 }}
            className="text-lg md:text-2xl mb-12 max-w-2xl text-gray-300"
          >
            Join us for the biggest cultural festival of the year
          </motion.p>
          <motion.div 
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.6 }}
            className="flex flex-col md:flex-row gap-4 md:gap-6 w-full md:w-auto px-4 md:px-0"
          >
            <Link 
              to="/register"
              className="group bg-[#FF3366] px-8 py-4 rounded-full font-semibold hover:bg-[#FF3366]/90 transition-all duration-300 transform hover:scale-105 text-center transition-gpu"
            >
              Register Now
            </Link>
            <Link
              to="/events"
              className="group px-8 py-4 rounded-full font-semibold border-2 border-white hover:bg-white hover:text-black transition-all duration-300 transform hover:scale-105 text-center transition-gpu"
            >
              Learn More
            </Link>
          </motion.div>
        </div>

        {/* Event Preview Cards */}
        <motion.div 
          initial={{ opacity: 0, y: 100 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.9 }}
          className="relative z-20 max-w-7xl mx-auto px-4 md:px-8 -mb-32"
        >
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-6">
            {[
              {
                name: "Cascades",
                image: IMAGES.events.cascades,
                date: "March 15"
              },
              {
                name: "Cultura",
                image: IMAGES.events.cultura,
                date: "March 16"
              },
              {
                name: "Onam",
                image: IMAGES.events.onam,
                date: "March 17"
              },
              {
                name: "Kannada Rajotsava",
                image: IMAGES.events.kannadaRajotsava,
                date: "March 18"
              }
            ].map((event, index) => (
              <motion.div 
                key={index}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ delay: 0.2 + index * 0.1 }}
                className="group relative bg-gradient-to-b from-[#FF3366]/10 to-[#FF3366]/5 backdrop-blur-lg rounded-3xl p-4 transform hover:-translate-y-2 transition-all duration-300 transition-gpu"
                style={{
                  transform: `rotate(${index % 2 === 0 ? '2' : '-2'}deg)`
                }}
              >
                <div className="relative rounded-2xl overflow-hidden mb-4">
                  <div className="blur-load" style={{ backgroundImage: `url(${event.image}?blur=50)` }}>
                    <img 
                      src={event.image}
                      alt={event.name}
                      className={`w-full h-48 object-cover transform group-hover:scale-110 transition-transform duration-500 ${imagesLoaded ? 'opacity-100' : 'opacity-0'}`}
                      loading="lazy"
                    />
                  </div>
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent"></div>
                </div>
                <h3 className="text-xl font-bold mb-2">{event.name}</h3>
                <p className="text-[#FF3366]">{event.date}</p>
              </motion.div>
            ))}
          </div>
        </motion.div>
      </header>

      {/* Stats Section */}
      <section className="bg-[#0a0a0a] pt-48 pb-24">
        <div className="max-w-7xl mx-auto px-4 md:px-8">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-8">
            {[
              { number: "20K+", label: "Annual Attendees" },
              { number: "250+", label: "Performances" },
              { number: "100+", label: "Artists" },
              { number: "50+", label: "Awards" },
            ].map((stat, index) => (
              <motion.div 
                key={index}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ delay: index * 0.1 }}
                className="text-center transition-gpu"
              >
                <h3 className="text-4xl md:text-5xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-[#FF3366] to-[#FF9933]">
                  {stat.number}
                </h3>
                <p className="text-gray-400 mt-2 text-sm md:text-base">{stat.label}</p>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="py-8 px-4 md:px-8 border-t border-white/10">
        <div className="max-w-7xl mx-auto text-center text-gray-400">
          <p> 2024 Roy's Project CPA. All rights reserved.</p>
        </div>
      </footer>
    </div>
  );
}

export default Home;