import React from 'react';
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import { PartyPopper } from 'lucide-react';
import { useRegistrationStore } from '../store/registrationStore';

function RegistrationSuccess() {
  const navigate = useNavigate();
  const { registrationData } = useRegistrationStore();

  if (!registrationData) {
    navigate('/register');
    return null;
  }

  return (
    <div className="pt-32 pb-16">
      <div className="max-w-3xl mx-auto px-8">
        <motion.div
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: 1, scale: 1 }}
          className="text-center"
        >
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0, rotate: [0, 15, -15, 0] }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="inline-block mb-8"
          >
            <PartyPopper className="w-20 h-20 text-[#FF3366]" />
          </motion.div>
          
          <h1 className="text-6xl font-bold mb-6">Welcome to TARANGA!</h1>
          <p className="text-xl text-gray-400 mb-8">
            Thank you for registering, {registrationData.name}! We can't wait to see you at the event.
          </p>

          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.4 }}
            className="bg-gradient-to-br from-[#FF3366]/10 to-transparent p-8 rounded-3xl border border-white/10 mb-8"
          >
            <h2 className="text-2xl font-bold mb-4">Registration Details</h2>
            <div className="space-y-2 text-left">
              <p><span className="text-gray-400">Name:</span> {registrationData.name}</p>
              <p><span className="text-gray-400">Email:</span> {registrationData.email}</p>
              <p><span className="text-gray-400">College:</span> {registrationData.college}</p>
              <p><span className="text-gray-400">Number of Participants:</span> {registrationData.numberOfPeople}</p>
            </div>
          </motion.div>

          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.6 }}
            className="space-y-4"
          >
            <button
              onClick={() => navigate('/events')}
              className="bg-[#FF3366] px-8 py-4 rounded-xl font-semibold hover:bg-[#FF3366]/90 transition-all duration-300 transform hover:scale-105"
            >
              View Event Schedule
            </button>
          </motion.div>
        </motion.div>
      </div>
    </div>
  );
}

export default RegistrationSuccess;