/**
 * Constants for all image assets used in the   
 * Organized by category for better maintainability
 */

// Base path for all assets
const ASSETS_BASE_PATH = '/Assets';

// Image path generator utility
const getImagePath = (filename: string) => `${ASSETS_BASE_PATH}/${filename}`;

// Define image filenames by category
const ImageFiles = {
    logos: {
        main: 'cplogo.png',
        white: 'WHITE DSU CPA LOGO (without BG).png',
    },
    events: {
        cascades: 'cascades.jpg',
        cultura: 'cultura.jpg',
        onam: 'onam.jpg',
        kannadaRajotsava: 'kannada-rajotsava.jpg',
    },
    general: {
        concertCrowd: 'concert-crowd.jpg',
        teamCPA: 'team-cpa.jpg',
    }
} as const;

// Type definitions for strict type checking
type ImageCategory = typeof ImageFiles;
type LogoImages = { [K in keyof typeof ImageFiles.logos]: string };
type EventImages = { [K in keyof typeof ImageFiles.events]: string };
type GeneralImages = { [K in keyof typeof ImageFiles.general]: string };

interface Images {
    logos: LogoImages;
    events: EventImages;
    gallery: EventImages & GeneralImages;
    heroImage: string;
}

// Generate the final image paths
export const IMAGES: Images = {
    logos: {
        main: getImagePath(ImageFiles.logos.main),
        white: getImagePath(ImageFiles.logos.white),
    },
    events: {
        cascades: getImagePath(ImageFiles.events.cascades),
        cultura: getImagePath(ImageFiles.events.cultura),
        onam: getImagePath(ImageFiles.events.onam),
        kannadaRajotsava: getImagePath(ImageFiles.events.kannadaRajotsava),
    },
    gallery: {
        cascades: getImagePath(ImageFiles.events.cascades),
        cultura: getImagePath(ImageFiles.events.cultura),
        onam: getImagePath(ImageFiles.events.onam),
        kannadaRajotsava: getImagePath(ImageFiles.events.kannadaRajotsava),
        concertCrowd: getImagePath(ImageFiles.general.concertCrowd),
        teamCPA: getImagePath(ImageFiles.general.teamCPA),
    },
    heroImage: getImagePath(ImageFiles.general.concertCrowd),
};

export default IMAGES;
